


import React from "react";
import { useRouter } from "@/lib/hooks/useAppLocation";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import {
  Tooltip,
  TooltipContent,
  TooltipProvider,
  TooltipTrigger,
} from "@/components/ui/tooltip";
import {
  Search,
  LogOut,
  User as UserIcon,
  LogIn,
  ChevronLeft,
  ChevronRight,
} from "lucide-react";
import { useLayoutStore } from "@/lib/store/layoutStore";

//  Logo  ( )
import Logo from "@/assets/icons/js/2";
import textLogo from "@/assets/ .png";

type HeaderProps = {
  /**   */
  isLoggedIn: boolean;
  /**      (: "") */
  userName?: string;
  /**   PNG  ():    import textLogo  */
  companyPngSrc?: string;
  /**  () */
  onLogout?: () => void;
  onMyPage?: () => void;
  onLogin?: () => void;
};

export function Header({
  isLoggedIn,
  userName = "",
  companyPngSrc,
  onLogout,
  onMyPage,
  onLogin,
}: HeaderProps) {
  const router = useRouter();
  const [timeLeft, setTimeLeft] = React.useState<number>(
    isLoggedIn ? 600 : 0
  );
  const { isSidebarExpanded, toggleSidebar } = useLayoutStore();

  const handleExtend = () => {
    setTimeLeft(600);
  };

  React.useEffect(() => {
    if (!isLoggedIn) {
      setTimeLeft(0);
      return;
    }

    const id = setInterval(() => {
      setTimeLeft((prev) => {
        if (prev <= 1) {
          clearInterval(id);
          if (onLogout) {
            onLogout();
          } else {
            setTimeout(() => router.push("/"), 1000);
          }
          return 0;
        }
        return prev - 1;
      });
    }, 1000);

    return () => clearInterval(id);
  }, [isLoggedIn, onLogout, router]);

  const mm = String(Math.floor(timeLeft / 60)).padStart(2, "0");
  const ss = String(timeLeft % 60).padStart(2, "0");
  const companyLogoSrc = companyPngSrc ?? textLogo;

  function Divider() {
    return (
      <span aria-hidden className="mx-4 select-none text-gray-400">
        |
      </span>
    );
  }

  return (
    <header
      role="banner"
      className="sticky top-0 z-50 h-[75px] bg-[#25292e] text-white"
    >
      <div className="relative mx-auto flex h-full items-center justify-between">
        <div className="flex items-center h-full">
          <div className="w-56 flex items-center gap-2 pl-6">
            <Logo size={40} color="#58bf83" />
            <span className="whitespace-nowrap text-2xl font-normal leading-none pb-0.5">
              Jany system
            </span>
          </div>
          <div className="absolute left-[242px] h-full flex items-center justify-center">
            <TooltipProvider>
              <Tooltip>
                <TooltipTrigger asChild>
                  <Button
                    size="icon"
                    className="size-7 rounded-2xl bg-[#1ec37b] text-white hover:bg-[#ffffff] hover:text-[#1ec37b] border-none"
                    onClick={toggleSidebar}
                  >
                    {isSidebarExpanded ? (
                      <ChevronLeft className="size-4" />
                    ) : (
                      <ChevronRight className="size-4" />
                    )}
                  </Button>
                </TooltipTrigger>
                <TooltipContent>
                  <p>{isSidebarExpanded ? "" : ""}</p>
                </TooltipContent>
              </Tooltip>
            </TooltipProvider>
          </div>
        </div>
        <div className="flex items-center pr-4">
          {isLoggedIn ? (
            <>
              <div className="flex items-center gap-x-2">
                <span className="whitespace-nowrap text-sm">{userName}</span>
                <span
                  className="whitespace-nowrap text-xs text-gray-300"
                  aria-label={`   ${mm}:${ss}`}
                  title="  "
                >
                  {mm}:{ss}
                </span>
                <Button
                  variant="ghost"
                  className="h-auto rounded-full border border-gray-500 px-3 py-1 text-xs text-white hover:bg-white/10 hover:text-white"
                  onClick={handleExtend}
                >
                   
                </Button>
              </div>

              <Divider />

              <Button
                variant="ghost"
                size="icon"
                className="rounded-full [&>svg]:!h-6 [&>svg]:!w-6"
                aria-label=""
                onClick={onLogout}
              >
                <LogOut />
              </Button>

              <Divider />

              <Button
                variant="ghost"
                size="icon"
                className="rounded-full [&>svg]:!h-6 [&>svg]:!w-6"
                aria-label=""
                onClick={onMyPage}
              >
                <UserIcon />
              </Button>

              <Divider />

              <div className="relative w-[260px] max-w-xs">
                <Search className="pointer-events-none absolute right-2.5 top-2 h-5 w-5 text-gray-400" />
                <Input
                  type="search"
                  className="w-full rounded-full bg-white pl-4 pr-12 text-gray-900"
                />
              </div>
            </>
          ) : (
            <>
              <Button
                variant="ghost"
                size="icon"
                className="rounded-full [&>svg]:!h-6 [&>svg]:!w-6"
                aria-label=""
                onClick={onLogin}
              >
                <LogIn />
              </Button>
              <div className="relative ml-3 w-[260px] max-w-xs">
                <Search className="pointer-events-none absolute right-2.5 top-2 h-5 w-5 text-gray-400" />
                <Input
                  type="search"
                  className="w-full rounded-full bg-white pl-4 pr-12 text-gray-900"
                />
              </div>
            </>
          )}
        </div>
      </div>
    </header>
  );
}
